* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Helvetica", "Arial", sans-serif;
}

.header {
  background-color: #ea5455;
  text-align: center;
  color: black;
  padding: 0.5rem 0;
}

.main-container {
  background-color: #e4dccf;
  display: flex;
  flex-direction: column;
}

.form-container,
.preview-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  margin: 2rem;
}

.inner-form-container {
  width: 90%;
  background-color: #f9f5eb;
  border-radius: 10px;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}

.education,
.experience {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.personal > h3,
.education-container > h3,
.experience > h3,
.experience-container > h3 {
  padding: 1rem 1rem 0 1rem;
}

.button {
  background-color: #ea5455;
  border: 1px solid var(--secondary-color);
  border-radius: 3px;
  box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
  width: 95%;
  padding: 0.5rem 0.5rem;
  font-size: 14px;
  font-weight: bold;
  color: wheat;
  margin-bottom: 0.5rem;
}

.button:hover {
  cursor: pointer;
}

.add {
  background-color: #1a6543;
}

.form {
  display: flex;
  flex-direction: column;
  padding: 1rem;
  gap: 1rem;
  width: 100%;
}

.form input[type="text"],
.form input[type="email"],
.form input[type="tel"] {
  background-color: #ffffff;
  border: 1px solid var(--secondary-color);
  border-radius: 3px;
  box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
  width: 100%;
  padding: 7px;
  font-size: 14px;
}

.form input[id="description"] {
  height: 4rem;
  vertical-align: top;
  resize: none;
}

.inner-preview-container {
  width: 95%;
  background-color: #f9f5eb;
  border-radius: 10px;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}

.cv-grid-container {
  display: grid;
  grid-template-areas: "title title title title" "info info info bio";
}

.cv-title {
  grid-area: title;
  background-color: #3a98b9;
}

.cv-bio {
  grid-area: bio;
  background-color: #fff1dc;
  padding: 2rem;
}

.cv-info {
  grid-area: info;
  background-color: #eeeeee;
}

.cv-title-name {
  padding: 20px 20px 10px 20px;
  color: rgb(238, 238, 238);
  font-size: 2rem;
}

.cv-title-job {
  padding: 0px 0px 20px 20px;
  color: rgb(238, 238, 238);
  font-size: 1.5rem;
}

.cv-bio-image {
  display: flex;
  align-items: center;
  justify-content: center;
}

.cv-bio-image > img {
  width: 150px;
}

.cv-bio-personal-title,
.cv-info-title {
  font-weight: bold;
  color: #3a98b9;
  margin-top: 1rem;
  border-bottom: solid;
}

.cv-bio-title {
  font-weight: bold;
  margin-top: 1rem;
  color: rgb(51, 51, 51);
}

.cv-bio-info {
  margin-top: 0.1rem;
}

.cv-info {
  padding: 1rem;
}

.cv-info-desc {
  color: rgb(51, 51, 51);
  padding-top: 1rem;
}

.cv-info-exp {
  display: flex;
  color: rgb(51, 51, 51);
}

.cv-info-timeline {
  font-weight: bold;
  font-size: 15px;
  padding: 1rem 3rem 1rem 0rem;
  flex: 1;
}

.cv-info-info {
  flex: 3;
}

.cv-info-exp-title {
  font-weight: bold;
  font-size: 15px;
  padding: 1rem 1rem 0.5rem 0rem;
}

.cv-info-edu-desc {
  margin: 0 0 0.2rem 0;
}

@media only screen and (max-width: 600px) {
  .cv-grid-container {
    display: grid;
    grid-template-areas: "title" "bio" "info";
  }
}
